<template>
  <div class="page-home">

    <section class="project">
      <h2>
        Projects
        <router-link to='/project'>
        <span class="more" data-name="learn">more<i class="iconfont icon-more"></i></span>
        </router-link>
      </h2>
      <div class="carousel" @mouseover="stop" @mouseleave="resize">
        <ul class="clearfix">
          <li v-for="(item, i) in projects" :key="i">
              <a target="_blank" :href="item.href" title="点击打开网页查看"><img :src="item.img" /></a>
          </li>
        </ul>
        <a class="arrow prev" @click="prev"><i class="iconfont icon-icon-test"></i></a>
        <a class="arrow next" @click="next"><i class="iconfont icon-icon-test1"></i></a>
      </div>
      
    </section>

    <!-- <section class="blog">
      <h2>
        My blogs
        <router-link to='/blog'>
        <span class="more" data-name="learn">more<i class="iconfont icon-more"></i></span>
        </router-link>
      </h2>
      <ul class="list">
          <li><a href=""></a></li>
          <li><a href=""></a></li>
          <li><a href=""></a></li>
      </ul>
    </section> -->

    <section class="study">
      <h2>
        Study
        <router-link to='/study'>
        <span class="more" data-name="learn">more<i class="iconfont icon-more"></i></span>
        </router-link>
      </h2>
      <ul class="items">
          <li>
              <p class="title" @click="change(0)">参考书目<i :class="['iconfont', list[0] ? 'icon-subtract' : 'icon-add']"></i></p>
              <ul class="sub" v-show="list[0]">
                <li v-for="(item, i) in books" :key="i">   
                  {{item.name}}
                </li>
            </ul>
          </li>
          <li>
              <p class="title" @click="change(1)">文档博客<i :class="['iconfont', list[1] ? 'icon-subtract' : 'icon-add']"></i></p>
              <ul class="sub" v-show="list[1]">
                <li v-for="(item, i) in docs" :key="i">
                    <a target="_blank" :href="item.href">
                        {{item.name + " : " + item.href}}
                    </a>
                </li>
            </ul>
          </li>
          <li>
              <p class="title" @click="change(2)">代码练习<i :class="['iconfont', list[2] ? 'icon-subtract' : 'icon-add']"></i></p>
              <ul class="sub" v-show="list[2]">
                <li v-for="(item, i) in codes" :key="i">
                    <a target="_blank" :href="item.href">
                        {{item.name + " : " + item.href}}
                    </a>
                </li>
              </ul>
          </li>
          <li>
              <p class="title" @click="change(3)">视频网站<i :class="['iconfont', list[3] ? 'icon-subtract' : 'icon-add']"></i></p>
              <ul class="sub" v-show="list[3]">
                <li v-for="(item, i) in videos" :key="i">
                    <a target="_blank" :href="item.href">
                        {{item.name + " : " + item.href}}
                    </a>
                </li>
              </ul>
          </li>
      </ul>
    </section>
  </div>
  
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      projects: [
        {
          href: './static/project-school/index.html',
          img: require('../assets/images/p-school.png')
        },
        {
          href: './static/project-business/index.html',
          img: require('../assets/images/p-business.png')
        },
        {
          href: './static/project-music/index.html',
          img: require('../assets/images/p-music.png')
        },
      ],
      books: [
        {
            name: '《CSS权威指南》', 
            img: require('../assets/images/css.jpg')
        },
        {
            name: '《CSS世界》', 
        img: require('../assets/images/css1.jpg')
        },
        {
            name: '《JavaScript高级程序设计》', 
            img: require('../assets/images/js.jpg')
        },
        {
            name: '《JavaScript权威指南》', 
            img: require('../assets/images/js1.jpg')
        },
        {
            name: '《ES6标准入门》', 
            img: require('../assets/images/js-es6.jpg')
        }
      ],
      docs: [
        {
            name: 'MDN Web 文档', 
            href: 'https://developer.mozilla.org/zh-CN/'
        },
        {
            name: 'The Modern JavaScript Tutorial',href: 'https://zh.javascript.info/'
        },
        {
            name: '阮一峰的网络日志', 
            href: 'http://www.ruanyifeng.com/blog/'
        },
        {
            name: '张鑫旭的个人主页', 
            href: 'https://www.zhangxinxu.com/'
        },
        {
            name: 'JavaScript秘密花园', 
            href: 'https://bonsaiden.github.io/JavaScript-Garden/zh/'
        }
      ],
      codes: [
        {
            name: 'edabit', 
            href: 'https://edabit.com'
        },
        {
            name: 'LeetCode', 
            href: 'https://leetcode.com/'
        },
        {
            name: 'freeCodeCamp', 
            href: 'https://www.freecodecamp.org'
        },
        {
            name: 'codewars', 
            href: 'https://www.codewars.com/dashboard'
        },
        {
            name: 'regex101', 
            href: 'https://regex101.com/'
        }
      ],
      videos: [
        {
            name: '慕课网', 
            href: 'https://www.imooc.com/'
        },
        {
            name: '哔哩哔哩', 
            href: 'https://www.bilibili.com/'
        }
      ],
      list: [true, true, true, true],
      interval: null
    }
  },
  mounted(){
    this.$nextTick(() => {
      this.play();
    })
  },
  methods: {
    change(num){
      this.list = this.list.map((c, i) => i == num ? !c : c);
    },
    play(){
      this.inerval = null;
      this.interval = setInterval(this.next, 3000)
    },
    stop(){
      clearInterval(this.interval);
      this.interval = null;
    },
    resize(){
      this.play();
    },
    prev(){
      this.projects.unshift(this.projects.pop());
    },
    next(){
      this.projects.push(this.projects.shift());
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* project carousel style */
.project .carousel .arrow i{
    font-size: 35px;
    font-weight: bold;
}
.project .carousel ul{
  width: 500%;
} 
.project .carousel ul li{
  width: 10%;
}
.project .carousel > ul > li img{
    display: block;
    width: 100%;
    padding: 0 10px;
}
@media screen and (max-width: 480px){
  .project .carousel ul{
    width: 500%;
  } 
  .project .carousel ul li{
    width: 20%;
  }
  .project .carousel > ul > li img{
    padding: 0;
  }
}



/* main blog style */
.blog .list{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.blog .list > li{
  width: 30%;
  padding: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}



/*study style */
.study .items{
  list-style-type: none;
  margin: 10px;
}
.study .items > li{
  display: flex;
  flex-wrap: nowrap;
  padding: 5px;
  border-top: 1px solid;
  border-top-color: #6d676e;
  border-top-color: var(--back-color, #6d676e);
}
.study .title{
  width: 12%;
  min-width: 100px;
  padding: 10px 0;
  color: #faa916;
  color: var(--sub-title-color, #faa916);
  font-size: 16px;
  line-height: 16px;
  cursor: pointer;    
}
.study .sub{
  width: 88%;
  /* display: none; */
}
.study .title i{
  display: inline-block;
  padding: 0 10px;
  color: var(--sub-title-color);
  font-size: 14px;
}
.study .sub li{
  margin: 5px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.study .sub a{
  color: #fbfffe;
  color: var(--text-color, #fbfffe);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.study .sub a:visited{
  color: #6d676e;
  color: var(--visited-color, #6d676e);
}
.study .sub a:hover{
  color: #faa916;
  color: var(--hover-color, #faa916);
}



@media screen and (max-width: 768px){
  .project .container figure{
      width: 49%;
  }
  .study .items > li{
      display: flex;
      flex-wrap: wrap;
  }
  .study .title,
  .study .sub{
      width: 100%;
  }
}
@media screen and (max-width: 480px){
  .project .container figure{
      width: 100%;
  }
}
</style>



