import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import project from '@/components/project'
import blog from '@/components/blog'
import study from '@/components/study'
import notFound from '@/components/notFound'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      alias: '/home',
      name: 'home',
      component: home,
      meta: {
        title: 'Home | 首页'
      }
    },
    {
      path: '/project',
      name: 'project',
      component: project,
      meta: {
        title: 'Project | 项目展示'
      }
    },
    {
      path: '/blog',
      name: 'blog',
      component: blog,
      meta: {
        title: 'Blog | 我的博客'
      }
    },
    {
      path: '/study',
      name: 'study',
      component: study,
      meta: {
        title: 'Study | 学习路径'
      }
    },
    {
      path: '*',
      name: '404',
      component: notFound,
      meta: {
        title: 'Not Found'
      }
    }
  ]
})



