<template>
    <div class="page-study">
        <section class="book">
            <h2>阅读书目<i class="iconfont icon-book"></i></h2>
            <div class="carousel" @mouseover="stop"  @mouseleave="resize">
                <ul class="items clearfix">
                    <li v-for="(item, i) in books" :key="i">
                        <div class="wrap">
                            <img :src="item.img" />
                            <div class="detail">
                                <p class="title">{{item.name}}</p>
                            </div>
                        </div>
                    </li>
                </ul>
                <a @click="prev" class="arrow prev"><i class="iconfont icon-icon-test"></i></a>
                <a @click="next" class="arrow next"><i class="iconfont icon-icon-test1"></i></a>
            </div>
        </section>

        <section class="doc">
            <h2>文档博客<i class="iconfont icon-Web"></i></h2>
            <ul class="list">
                <li v-for="(item, i) in docs" :key="i">
                    <a target="_blank" :href="item.href">
                        {{item.name + " : " + item.href}}
                    </a>
                </li>
            </ul>
        </section>

        <section class="code">
            <h2>代码练习<i class="iconfont icon-code"></i></h2>
            <ul class="list">
                <li v-for="(item, i) in codes" :key="i">
                    <a target="_blank" :href="item.href">
                        {{item.name + " : " + item.href}}
                    </a>
                </li>
            </ul>
        </section>

        <section class="video">
            <h2>视频网站<i class="iconfont icon-video"></i></h2>
            <ul class="list">
                <li v-for="(item, i) in videos" :key="i">
                    <a target="_blank" :href="item.href">
                        {{item.name + " : " + item.href}}
                    </a>
                </li>
            </ul>
        </section>

        <section class="other">
            <h2>其他内容<i class="iconfont icon-other"></i></h2>
            <ul class="list">
                <li v-for="(item, i) in others" :key="i">
                    <a target="_blank" :href="item.href">
                        {{item.name + " : " + item.href}}
                    </a>
                </li>
            </ul>
        </section>
    </div>
</template>
<script>
export default {
    name: 'study',
    data(){
        return {
            books: [
                {
                    name: '《CSS权威指南》', 
                    img: require('../assets/images/css.jpg')
                },
                {
                    name: '《CSS世界》', 
                    img: require('../assets/images/css1.jpg')
                },
                {
                    name: '《JavaScript高级程序设计》', 
                    img: require('../assets/images/js.jpg')
                },
                {
                    name: '《JavaScript权威指南》', 
                    img: require('../assets/images/js1.jpg')
                },
                {
                    name: '《ES6标准入门》', 
                    img: require('../assets/images/js-es6.jpg')
                }
            ],
            docs: [
                {
                    name: 'MDN Web 文档', 
                    href: 'https://developer.mozilla.org/zh-CN/'
                },
                {
                    name: 'The Modern JavaScript Tutorial',href: 'https://zh.javascript.info/'
                },
                {
                    name: '阮一峰的网络日志', 
                    href: 'http://www.ruanyifeng.com/blog/'
                },
                {
                    name: '张鑫旭的个人主页', 
                    href: 'https://www.zhangxinxu.com/'
                },
                {
                    name: 'JavaScript秘密花园', 
                    href: 'https://bonsaiden.github.io/JavaScript-Garden/zh/'
                }
            ],
            codes: [
                {
                    name: 'edabit', 
                    href: 'https://edabit.com'
                },
                {
                    name: 'LeetCode', 
                    href: 'https://leetcode.com/'
                },
                {
                    name: 'freeCodeCamp', 
                    href: 'https://www.freecodecamp.org'
                },
                {
                    name: 'codewars', 
                    href: 'https://www.codewars.com/dashboard'
                },
                {
                    name: 'regex101', 
                    href: 'https://regex101.com/'
                }
            ],
            videos: [
                {
                    name: '慕课网', 
                    href: 'https://www.imooc.com/'
                },
                {
                    name: '哔哩哔哩', 
                    href: 'https://www.bilibili.com/'
                }
            ],
            others: [
                {
                    name: 'Github', 
                    href: 'https://github.com/'
                },
                {
                    name: '阿里巴巴矢量图标库', 
                    href: 'https://www.iconfont.cn/'
                }
            ],
            interval: null
        }
    },
    mounted(){
        this.$nextTick(() => {
            this.play();
        })
    },
    methods: {
        play(){
            this.inerval = null;
            this.interval = setInterval(this.next, 3000)
        },
        stop(){
            clearInterval(this.interval);
            this.interval = null;
        },
        resize(){
            this.play();
        },
        prev(){
            this.books.unshift(this.books.pop());
        },
        next(){
            this.books.push(this.books.shift());
        }
    }
}
</script>
<style scoped>
/*book style */
section h2 .iconfont{
    font-size: inherit;
    font-weight: normal;
    padding: 0 10px;
}
/*book carousel style */
.book .carousel .arrow i{
    font-size: 35px;
    font-weight: bold;
}
.book .carousel ul{
    width: 500%;
    padding: 10px 0;
    background-color: #464246;
    background-color: var(--back-color);
}
.book .carousel > ul > li{
    width: 5%;
}
/* .book .carousel > ul > li img{
    display: block;
    width: 100%;
} */
@media screen and (max-width: 768px){
  .book .carousel ul{
    width: 500%;
  } 
  .book .carousel ul li{
    width: 10%;
  }
  .book .carousel > ul > li img{
    padding: 0;
  }
}
/* 3d旋转动画 */
.book .carousel .items > li{
    perspective: 1000px;
    padding: 5px;
}
.book .carousel .items .wrap{
    position: relative;
    /*应用于父容器，用于制造3D效果*/
    transform-style: preserve-3d;
    transition: transform 1s;
}
.book .carousel .items .detail{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #1b1b1e;
    background-color: var(--front-color, #1b1b1e);
    /*简写和分开写效果相差很大*/
    transform: rotateY(0.5turn) translateZ(1px);
    overflow: hidden;
    display: table;
}
.book .carousel .items .detail .title{
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    /* padding-top: 30%; */
}
.book .carousel .items > li:hover .wrap{
    transform: rotateY(0.5turn);
}
.book .carousel .items > li:hover .detail{
    opacity: 0.9;
    transition: opacity 0.3s;
}



/*doc, code, video, other list style */
section .list{
    list-style-type: none;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}
section .list li{
    width: 45%;
    margin: 5px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
section .list li a:visited{
    color: #6d676e;
    color: var(--visited-color, #6d676e);
}
section .list li a:hover{
    color: #faa916;
    color: var(--hover-color, #faa916);
}



@media screen and (max-width: 768px){
     section .list li{
        width: 100%;
    }
}
</style>


