<template>
  <div id="app">

    <header>
      <h1>{{ $route.meta.title }}</h1> 
      <div class="list" @click="show"><i class="iconfont icon-list"></i></div>
      <nav ref="nav">
        <ul class="menu">
          <li class="cancel" @click="hide"><i class="iconfont icon-cancel"></i></li>
          <router-link v-for="(item, i) in menus" :to="item.path" :key="i">
            <li :class="{active: item.path == $route.path}">
                <i :class="['iconfont', item.icon]"></i><span class="page">{{ item.name }}</span>
            </li>
          </router-link>
        </ul>
      </nav>
    </header>

    <div id="main">
      <router-view/>
    </div>
    
    <footer>
      <a target="_blank" class="github" href="https://github.com/xiongerya"><i class="iconfont icon-githublogo"></i></a>
      <p>Copyright © 2019-2020 xiongerya</p>
      <p>View on <a target="_blank" class="link" href="https://github.com/xiongerya/xiongerya.github.io">Github</a></p>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      menus: [
        {name: 'home', path: '/', icon:'icon-home'},
        {name: 'project', path: '/project', icon:'icon-project'},
        // {name: 'blog', path: '/blog', icon:'icon-blog'},
        {name: 'study', path: '/study', icon:'icon-study'}
      ],
      width: document.body.clientWidth,
      isHide: true
    }
  },
  methods: {
    hide(){
      this.$refs.nav.style.left = "-100%";
    },
    show(){
      this.$refs.nav.style.left = "0";
    }
  },
  watch: {
    width(val){
      if(val <= 480){
        this.hide();
      }else{
        this.show();
      }
    },
    '$route'(){
      if(this.width <= 480){
        this.hide();
      }else{
        this.show();
      }
    }
  },
  mounted(){
    window.onresize = () => {
      this.width = document.body.clientWidth;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  /* color: #2c3e50; */
  /* margin-top: 60px; */
}



:root{
  --front-color: #302133;
  --back-color: #554958;
  --title-color: #f84842;
  --sub-title-color: #ffc417;
  --text-color: #f5f8f8;
  --link-color: #ffc417;
  --visited-color: #9f85a0;
  --hover-color: #ffc417;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1.6;
}
body{
  background-color: #464246;
  background-color: var(--back-color, #464246);
  color: #fbfffe;
  color: var(--text-color,#fbfffe);
  font-size: 14px;
  font-family: sans-serif;
  width: 100%;
}
a{
  color: #fbfffe;
  color: var(--text-color, #fbfffe);
  text-decoration: none;
}



header{
  width: 100%;
  padding: 15px 0;
  background-color: #1b1b1e;
  background-color: var(--front-color, #1b1b1e);
  color: #faa916;
  color: var(--title-color, #faa916);
  position: relative;
}
header h1{
  width: 80%;
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 20px;
  font-size: 28px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}



/* list style */
header .list{
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  margin: 15px 0;
  text-align: center;
  cursor: pointer;
}
header .list i{
  padding: 0 10px;
  font-size: 28px;
}



nav{
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  height: 100%;
  border-right: 1px solid;
  border-right-color: #464246;
  border-right-color: var(--back-color, #464246);
  background-color: #1b1b1e;
  background-color: var(--front-color, #1b1b1e);
  /* opacity: 0.5; */
}
nav .menu .cancel{
  display: none;
  font-size: 32px;
}
/* menu style */
nav .menu li{
  padding: 15px 0;
  text-align: center;
  cursor: pointer;
}
nav .menu i{
  display: inline-block;
  padding: 0 10px;
  font-size: 28px;
}
nav .menu .page{
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  width: 0;
  opacity: 0;
  transition: width 0.2s 0s, opacity 0s -0.1s;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
nav:hover .menu .page{
  width: 65px;
  opacity: 1;
  transition: width 0.2s 0s, opacity 0s 0.1s;   
}



footer{
  width: 100%;
  /* position: fixed;
  left: 0;
  bottom: 0; */
  margin: 15px 0;
  color: #fbfffe;
  color: var(--text-color, #fbfffe);
  text-align: center;
}
footer .icon-githublogo{
  font-size: 36px;
}

footer a.github:hover{
  color: #faa916;
  color: var(--sub-title-color, #faa916); 
}

footer a.link{
  color: #faa916;
  color: var(--sub-title-color, #faa916); 
}



/* 中间载入页面公共样式 */
#main{
  width: 80%;
  max-width: 1400px;
  margin: 0 auto;
  /* overflow: hidden; */
}
/* section style */
section{
  padding: 20px;
  margin: 20px 0;
  border-radius: 10px;
  background-color: #1b1b1e;
  background-color: var(--front-color, #1b1b1e);
}
section h2{
  margin-bottom: 10px;
  letter-spacing: 1px;
  color: #fbfffe;
  color: var(--sub-title-color, #fbfffe);
}
section .more{
  float: right;
  padding: 0 20px;
  color: #ffc417;
  color: var(--sub-title-color, #ffc417);
  font-size: 14px;
  font-weight: normal;
  letter-spacing: 0;
  cursor: pointer;
}



.clearfix::after{
    content: "";
    display: block;
    clear: both;
}
.active{
  color: var(--title-color);
}
.hide{
  display: none;
}



/* 轮播图公共样式 */
.carousel{
    position: relative;
    overflow: hidden;
}
.carousel .arrow{
    height: 100%;
    position: absolute;
    top: 0;
    overflow: hidden;
    cursor: pointer;
    display: table;
}
.carousel .arrow i{
    display: table-cell;
    vertical-align: middle;
    color: var(--front-color);
}
.carousel .prev{
    left: 0;
}
.carousel .next{
    right: 0;
}
.carousel ul{
    list-style-type: none;
    position: relative; 
    left: 0;
    top: 0;
    background-color: #464246;
    background-color: var(--back-color);
}
.carousel > ul > li{
    float: left;
}
.carousel > ul > li img{
    display: block;
    width: 100%;
}



/* @meida 媒体查询实现自适应布局 */
/* ipad pro 及以下尺寸布局 */
@media screen and (max-width: 1024px){
   header h1{
      width: 100%;
      padding-left: 70px;
    }
    nav .menu .page{
      display: none;
      width: 0;
    }
    #main{
      width: 100%;
      padding-left: 60px;
    }
    footer{
      padding-left: 60px;
    }
}
/* 移动端尺寸布局 */
@media screen and (max-width: 480px){
   header h1{
      width: 100%;
      padding-left: 70px;
    }
    nav{
      left: -100%;
      transition: left 0.3s;
    }
    nav .menu .cancel{
      display: block;
    }
    nav .menu .page{
      display: inline-block;
      width: 65px;
      opacity: 1;
    }
    #main{
      width: 100%;
      padding-left: 0;
    }
    footer{
      padding-left: 0;
    }
}
</style>



