<template>
  <div class="page-project">
    <section>
      <h2>学校官网</h2>
      <div class="container">
          <figure>
              <a target="_blank" href="./static/project-school/index.html" title="点击打开网页查看"><img src="../assets/images/p-school.png" /></a>
              <figcaption><a class="blank" href="http://www.ntu.edu.cn/main.htm">点击跳转源地址</a></figcaption>
          </figure>
          <div class="info">
              <h3>技术&难点</h3>
              <ul class="list">
                  <li>display:flex + 百分比width + @media实现不同屏幕宽度下的自适应布局</li>
                  <li>利用border不同方向的width，color实现了一个CSS三角形</li>
                  <li>background-image + background-position：定位背景图片，加载icon图标</li>
                  <li>JavaScript原生代码实现了一个无缝切换的焦点轮播图</li>
                  <li>JavaScript原生代码实现点击滚动回到顶部效果</li>
              </ul>
          </div>
      </div>
    </section>
    
    <section>
      <h2>电商首页</h2>
      <div class="container">
          <figure>
              <a target="_blank" href="./static/project-business/index.html" title="点击打开网页查看"><img src="../assets/images/p-business.png" /></a>
              <figcaption><a class="blank" href="https://www.jd.com/">点击跳转源地址</a></figcaption>
          </figure>
          <div class="info">
              <h3>技术&难点</h3>
              <ul class="list">
                  <li>position + z-index:实现了多级菜单功能，以及堆叠层级上下文</li>
                  <li>position定位 + DOM元素操作：实现侧边栏的显示/隐藏效果</li>
                  <li>JavaScript原生代码实现轮播图，倒计显示和tab栏点击切换效果</li>
                  <li>JavaScript原生代码操作DOM元素，实现楼层滚动切换的效果</li>
              </ul>
          </div>
      </div>
    </section>

    <section>
      <h2>音乐播放器</h2>
      <div class="container">
          <figure>
              <a target="_blank" href="./static/project-music/index.html" title="点击打开网页查看"><img src="../assets/images/p-music.png" /></a>
          </figure>
          <div class="info">
              <h3>技术&难点</h3>
              <ul class="list">
                  <li>利用rem和@media实现一定宽高比的自适应布局</li>
                  <li>position + transform：实现元素的水平垂直居中效果</li>
                  <li>animation + @keyframes实现音乐播放时图片旋转的动画效果</li>
                  <li>font-awesome代替icon图标使用，可设置font-size和font-color</li>
                  <li>Vue响应式数据的特点，使用v-for将获取的数据传递渲染到DOM上</li>
              </ul>
          </div>
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    name: 'project',
    data() {
      return {
        value: 1
      }
    }
  };
</script>
<style scoped>
section{
    overflow: hidden;
    position: relative;
}
section .expand{
    text-align: right;
}
section .expand span{
    display: inline-block;
    padding: 5px 3px;
    color: #faa916;
    color: var(--sub-title-color, #faa916);
    font-weight: bold;
    cursor: pointer;   
}
section .container{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: stretch;
}
section .container figure{
    width: 40%;
    padding-bottom: 10px;
}
section .container .info{
    width: 55%;
}
section .container figcaption{
    text-align: center;
}
section .container figcaption a{
    color: #6d676e;
    color: var(--text-color, #6d676e);
}
section .container .link{
    display: inline-block;
}
section .container img{
    width: 100%;
}
section .container .info h3{
    color: #faa916;
    color: var(--sub-title-color, #faa916);
}
section .container .info .list{
    margin: 0 20px; 
}
section .container .info .list > li{
    margin: 10px 0;
}



@media screen and (max-width: 768px){
    section .container figure,
    section .container .info{
        width: 100%;
    }
}
</style>


